import React from 'react'
import Button, { BUTTON_SIZE, BUTTON_TYPE } from '../../h5/component/button'
import Icon from '../../h5/component/icon'

export default class ButtonExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <Button></Button>
                <br/>
                <Button inline></Button>
                <div></div>
                <br/>
                <Button buttonColor={'#ff4f4f'}></Button>
                <br/>
                <Button buttonStyle={{backgroundColor: '#3eaf7c', borderColor: '#3eaf7c'}}></Button>
                <br/>
                <Button inline size={BUTTON_SIZE.SMALL} buttonStyle={{marginRight: '0.2rem'}}></Button>
                <Button inline size={BUTTON_SIZE.MIDDLE} buttonStyle={{marginRight: '0.2rem'}}></Button>
                <Button inline size={BUTTON_SIZE.BIG}></Button>
                <div></div>
                <br/>
                <Button type={BUTTON_TYPE.GHOST}></Button>
                <br/>
                <Button textStyle={{color: '#333'}}></Button>
                <br/>
                <Button disabled></Button>
                <br/>
                <Button icon={<Icon name={'ios-add'} color={'white'}/>}></Button>
                <br/>
                <Button onPress={() => alert('click')}></Button>
            </div>
        )
    }
}


