import React from 'react'
import Search from '../../h5/component/search'

export default class SearchExample extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: ''
        }
    }
    render() {
        return (
            <div className="container">
                <Search
                    placeholder = {''}
                />
                <br/>
                <Search
                    placeholder = {''}
                    showCancelButton
                />
                <br/>
                <Search
                    placeholder = {''}
                    showCancelButton
                    cancelText = {''}
                />
                <br/>
                <Search
                    placeholder = {''}
                    showModal
                />
                <br/>
                <Search
                    placeholder = {''}
                    showModal
                    modalComponent = {<p style={{fontSize: '0.4rem'}}></p>}
                />
                <br/>
                <Search
                    placeholder = {''}
                    disabled
                />
                <br/>
                <Search
                    defaultValue = {''}
                />
                <br/>
                <Search
                    value = {this.state.value}
                    onChange = {value => this.setState({value})}
                />
            </div>
        )
    }
}


