import React from 'react'
import Header from '../../h5/component/header'
import Icon from '../../h5/component/icon'

export default class HeaderExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <p className={'label'}></p>
                <Header title = {''}/>
                <br/>
                <p className={'label'}></p>
                <Header
                    title = {''}
                    leftBtn = {<Icon name={'ios-arrow-back'} color={'white'}/>}
                />
                <br/>
                <p className={'label'}></p>
                <Header
                    title = {''}
                    rightBtn = {<span style = {{fontSize: '0.35rem', color: 'white'}} ></span>}
                />
                <br/>
                <p className={'label'}></p>
                <Header
                    title = {''}
                    containerStyle = {{backgroundColor: '#01DD9B'}}
                    leftBtn = {<Icon name={'ios-arrow-back'} color={'white'}/>}
                    rightBtn = {<span style = {{fontSize: '0.35rem', color: 'white'}} ></span>}
                />

            </div>
        )
    }
}


